import { useState, useEffect } from 'react';
import { Button, Card, CardActions, CardContent, Input, TextField } from '@mui/material';

import $ from 'jquery';
import { saveAs } from 'file-saver';

import './Kanban.css';



export default function Kanban(){
	const MaxCards = 10000, MaxColumnItems = 10, MaxCardItems = 100;
	const [kanbanBoardState, setKanbanBoardState] = useState({columnMeta: {lastColumnId: 0}, columns: []});
	//const [focusEle, setFocusEle] = useState(null);
	const [getRequestNum, setGetRequestNum] = useState(0);
	const [sandbox, setSandbox] = useState(null);
	const [importKanbanBoard, setImportKanbanBoard] = useState(null);

	//get network request for kanbanBoardState
        function getRequestKanbanBoard(setKanban, getRequest, setGetRequest){
        	$.get("/api/kanbanState.php", function(response, status){
        	    if (response.includes("columnMeta")){
        	    	setKanban(JSON.parse(response));
        	    }	
        		
        		setGetRequest(getRequest + 1);

        	}, 'text');
        } 

        useEffect(() => {
            getRequestKanbanBoard(setKanbanBoardState, getRequestNum, setGetRequestNum);
        }, []);

        //update network request for kanbanBoardState
        function updateRequestKanbanBoard(dataObj){
        	let data = JSON.stringify(dataObj);
        	$.post("/api/kanbanState.php", data, function(response, status){}, 'json');
        } 

        useEffect(() => {
        	if (getRequestNum > 0){
        		updateRequestKanbanBoard(kanbanBoardState);
        	}
            
        }, [kanbanBoardState]);

        //common function for setKanbanBoardState
	function resetKanbanBoardState(kanbanBoardState, temp){
		setKanbanBoardState({columnMeta: kanbanBoardState.columnMeta, columns: temp});
	}

        //add and delete column and card
	function addColumn(){
		let temp = kanbanBoardState.columns;
		let tempColumn = {};

		tempColumn['id'] = kanbanBoardState.columnMeta.lastColumnId + 1;
		kanbanBoardState.columnMeta.lastColumnId++;
		tempColumn['title'] = {id: tempColumn['id'] * MaxColumnItems + 1 , content: 'column title'};
		tempColumn['cardMeta'] = {lastChildId: tempColumn['id'] * MaxCards};
		tempColumn['cards'] = [];
		temp.push(tempColumn);
		resetKanbanBoardState(kanbanBoardState, temp);
	}

	function deleteColumn(event){
		let temp = kanbanBoardState.columns;
		for (let col of temp){
				if (col.id.toString() == event.target.id){
					let index = temp.indexOf(col);
					temp.splice(index, 1);
				} 
		}

		resetKanbanBoardState(kanbanBoardState, temp);
	}

	function addCard(event){
		let temp = kanbanBoardState.columns;
		for (let col of temp){
			if (col.id.toString() == event.target.id){
			     let card = {};
			     card['id'] = col.cardMeta.lastChildId + 1;
			     col.cardMeta.lastChildId++;
			     card['title'] = {id: card['id'] * MaxCardItems + 1, content: 'card title'};
			     card['content'] = {id: card['id'] * MaxCardItems + 2, content: 'card content'};
			     col.cards.push(card);
			} 
		}

		resetKanbanBoardState(kanbanBoardState, temp);
	}

	function addCardByDrop(kanbanBoardState, columnId, card){
		let temp = kanbanBoardState.columns;
		for (let col of temp){
			if (col.id.toString() == columnId){
			     card['id'] = col.cardMeta.lastChildId + 1;
			     col.cardMeta.lastChildId++;
			     card['title'] = {id: card['id'] * MaxCardItems + 1, content: card.title.content};
			     card['content'] = {id: card['id'] * MaxCardItems + 2, content: card.content.content};
			     col.cards.push(card);
			} 
		}

		resetKanbanBoardState(kanbanBoardState, temp);
	}

	function deleteCard(event){
		let temp = kanbanBoardState.columns;
		for (let col of temp){
			if (col.id == Math.floor(parseInt(event.target.id) / MaxCards)){
				for (let card of col.cards){
					if (card.id.toString() == event.target.id){
						let index = col.cards.indexOf(card);
						col.cards.splice(index, 1);
					}
				}
			} 
		}

		resetKanbanBoardState(kanbanBoardState, temp);
	}
        
	function deleteCardById(kanbanBoardState, cardId){
		let temp = kanbanBoardState.columns;
		let deletedCard = null;
		for (let col of temp){
			if (col.id == Math.floor(parseInt(cardId) / MaxCards)){
				for (let card of col.cards){
					if (card.id.toString() == cardId){
						deletedCard = card;
						let index = col.cards.indexOf(card);
						col.cards.splice(index, 1);
					}
				}
			} 
		}

		resetKanbanBoardState(kanbanBoardState, temp);

		return deletedCard;
	}

        //handle input change value
	function changeValue(event){
        let temp = kanbanBoardState.columns;
        for (let col of temp){
        	if (col.title.id.toString() == event.target.id){
                col.title.content = event.target.value;
        	} else {
        		for (let card of col.cards){
        			if (card.title.id.toString() == event.target.id){
        				card.title.content = event.target.value;
        			} else if (card.content.id.toString() == event.target.id){
        				card.content.content = event.target.value;
        			}
        		}
        	}
        }

        resetKanbanBoardState(kanbanBoardState, temp);
	}

        //drag and drop
        function handleDragStart(event){
        	event.dataTransfer.setData("text/plain", event.target.id);

        }

        function handleDrag(event){

        }

        function handleDragEnter(event){
        	event.preventDefault();

        }

        function handleDragOver(event){
        	event.preventDefault();

        }

        function handleDragLeave(event){

        }

        function handleDragEnd(event){

        }

        function handleDrop(event){
        	const cardId = event.dataTransfer.getData("text/plain");
        	let deletedCard = deleteCardById(kanbanBoardState, cardId);
        	addCardByDrop(kanbanBoardState, event.currentTarget.id, deletedCard);
        	event.preventDefault();
        }

        //upload plugin
        function uploadPlugin(event){
        	//const file = document.querySelector('#upload-Kanban-plugin').files[0];
        	const file = event.target.files[0];
        	const formData = new FormData();
        	formData.set("file", file);

        	const xhr = new XMLHttpRequest();
        	xhr.open("POST", "/api/plugin.php");
        	xhr.send(formData);
        }

        //import and export kanban board
        function handleExport(event){
        	var content = JSON.stringify(kanbanBoardState);
        	var filename = "cspm-kanban-board.cspm";
        	var blob = new Blob([content], {type: "text/plain;charset=utf-8"});
        	saveAs(blob, filename);
        }

        function uploadKanbanBoard(event){
        	setSandbox(window.open("https://cspm-docker-ndc5f4lmvq-ue.a.run.app"));

        	const reader = new FileReader();
        	reader.onload = function(event) {
        		setImportKanbanBoard(event.target.result);
        	};
        	reader.readAsText(event.target.files[0]);

        }

        function postImport(){
        	sandbox.postMessage(importKanbanBoard, "https://cspm-docker-ndc5f4lmvq-ue.a.run.app");
        }

        function handleImportUpdate(){
        	setKanbanBoardState(JSON.parse(importKanbanBoard));
        }
 
	return(
		<div id="kanban" style={{height: window.screen.height + "px"}}>
		<div id="kanban-first-row">
		<Button className='button-menu' variant="outlined" onClick={handleExport}>export kanban board</Button>
		<Button className='button-menu' variant="text">upload kanban board (.cspm):</Button>
		<Input id='upload-Kanban-plugin' type='file' onChange={uploadKanbanBoard} label='upload kanban board'/>
		<Button className='button-menu' variant="outlined" onClick={postImport}>update sandbox</Button>
		<Button className='button-menu' variant="outlined" onClick={handleImportUpdate}>update kanban board</Button>
		</div>
		<div id="kanban-second-row">
		<Button className='button-menu' variant="text">upload plugin (.php):</Button>
		<Input id='upload-Kanban-plugin' type='file' onChange={uploadPlugin} label='upload plugin'/>
		</div>
		<div id="kanban-third-row">
		<Button className='button' variant="contained" onClick={addColumn}>add list</Button>
		</div>
		<div id="kanban-board">
        {kanbanBoardState.columns.map((column) => ( 
        	        <div id={column.id.toString()} onDragEnter={handleDragEnter} onDrop={handleDrop} onDragOver={handleDragOver} onDragLeave={handleDragLeave}>
        		<Card className='column'>
        			<CardContent sx={{ backgroundColor: '#96969633', display: 'flex', flexDirection: 'column'}}>
        			<TextField id={column.title.id.toString()} className='input-text' variant="standard" value={column.title.content} onChange={changeValue}></TextField>
        			<Button id={column.id.toString()} className='button' variant="contained" onClick={addCard}>add card</Button>
        			<Button id={column.id.toString()} className='button' variant="contained" onClick={deleteColumn}>delete list</Button>
        			{column.cards.map((card) => (
        				<div id={card.id.toString()} draggable onDragStart={handleDragStart} 
        				onDrag={handleDrag} onDragEnd={handleDragEnd}>
        				<Card id={card.id.toString()}  className='card'>
        			<CardContent sx={{ display: 'flex', flexDirection: 'column' }}>
        			<TextField id={card.title.id.toString()} className='input-text' variant="standard" label="card title" value={card.title.content} onChange={changeValue}></TextField>
        			<TextField id={card.content.id.toString()} className='input-text' variant="standard" label="card content" value={card.content.content} onChange={changeValue}></TextField>
        			<Button id={card.id.toString()} className='button' variant="contained" onClick={deleteCard}>delete card</Button>
        			</CardContent>
        			</Card>
        			</div>
        				))}
        			</CardContent>
        			</Card>
        			</div>
        	
        			
        		))}
		</div>
		</div>
		);
}